import dayjs from "dayjs";
import React from "react";
import ReactTooltip from "react-tooltip";
import { Draggable, Droppable } from "react-drag-and-drop";

export default function Day(props) {
  const {
    date,
    isFirstDay,
    addOrEditEvent,
    eventsInDay,
    handleDrop,
    checkForExistingMeetings,
  } = props;

  return (
    <div className="col border">
      <div className="day-heading">{date.format("DD MMM YYYY")}</div>
      <div className="day-heading">{date.format("dddd")}</div>
      <div className="half-hours">
        {new Array(48).fill(1).map((it, idx) => {
          const slot = date.startOf("day").add(idx * 30, "minutes");
          return (
            <Droppable
              types={["event"]}
              onDrop={(e) => {
                handleDrop(slot, e.event);
              }}
              key={slot.$d.getTime()}
            >
              <div
                className={`half-hour half-hour-${idx % 2}`}
                key={slot.$d.getTime()}
                onClick={(e) => addOrEditEvent({ slot })}
              >
                {isFirstDay && !(idx % 2) ? (
                  <span className="half-hour-time">
                    {slot.format("hh:mm A")}
                  </span>
                ) : null}
              </div>
            </Droppable>
          );
        })}
        {eventsInDay.map((it) => {
          const conflicts = checkForExistingMeetings(it, it.eventId, true);
          const sortedEventKeys = [...conflicts, it.eventId].sort((a, b) =>
            a.localeCompare(b)
          );
          const positionForCurrentEvent = sortedEventKeys.indexOf(it.eventId);
          return (
            <div
              key={it.eventId}
              className="event-in-day"
              style={getStyles(it, conflicts, positionForCurrentEvent, date)}
              data-for={it.eventId}
              data-tip
              onClick={(e) => addOrEditEvent({ ...it })}
            >
              <Draggable type="event" data={it.eventId}>
                <div className="event-content">
                  <div className="event-title">{it.title}</div>
                  <div className="event-time">
                    {dayjs(it.startDate).format("hh:mm")} -{" "}
                    {dayjs(it.endDate).format("hh:mm A")}
                  </div>
                </div>
              </Draggable>

              <ReactTooltip
                id={it.eventId}
                effect="solid"
                clickable
                offset={{ top: 0, left: 0 }}
              >
                <p>Title: {it.title}</p>
                <p>Owner: {it.owner}</p>
                <p>
                  Time: {dayjs(it.startDate).format("DD MMM YYYY hh:mm A")} -{" "}
                  {dayjs(it.endDate).format("DD MMM YYYY hh:mm A")}
                </p>
                {it.theme ? <p>Theme: {it.theme}</p> : null}
              </ReactTooltip>
            </div>
          );
        })}
      </div>
    </div>
  );
}

export const getStyles = (event, conflicts, postion, date) => {
  const startOfDay = date.startOf("day").$d.getTime();
  const endOfDay = date.endOf("day").$d.getTime();
  const eventStartDate = Math.max(event.startDate, startOfDay);
  const eventEndDate = Math.min(event.endDate, endOfDay);
  const height =
    ((eventEndDate - eventStartDate) / (1000 * 60 * 30)) * 20 + "px";

  const top =
    ((eventStartDate - date.startOf("day").$d.getTime()) / (1000 * 60 * 30)) *
      20 +
    "px";

  const left = (100 / (conflicts.length + 1)) * postion + "%";

  const width = 100 / (conflicts.length + 1) + "%";

  return {
    width,
    top,
    height,
    left,
  };
};
